import './components.css'
import React, {Fragment} from 'react'

const liContainer = {
    width: "160px",
    height: "150px",
    margin: "20px",
    backgroundColor: "#ffff88",
    padding: "20px",
    overflow: "hidden",
    // overflowY: "scroll",
    borderRadius: "5px",
    boxShadow: "10px 10px 15px rgba(215, 215, 215, 0.8)",
}

const liContainerDone = {
    width: "160px",
    height: "150px",
    margin: "20px",
    backgroundColor: "#cccccc",
    padding: "20px",
    overflow: "hidden",
    // overflowY: "scroll",
    borderRadius: "5px",
    boxShadow: "10px 10px 15px rgba(215, 215, 215, 0.8)",
    textDecoration: "line-through",
    color: "rgb(0,0,0,0.4)"
}

const liInput = {
    width: "100%",
}

const contentAreaStyle = {
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-between",
    height: "100%"
}

const textStyle = {
    wordWrap: "break-word"
}

const buttonGroupStyle = {
    // alignSelf: "flex-end"
    textAlign: "center",
    // width: "100%"
    // display: "flex"
}

const tickButtonStyle = {
    border: "none",
    cursor: "pointer",
    padding: "5px 10px",
    fontSize: "10px",
    display: "inline-block",
    color: "white"
}

const ListItem = (props) => {

    // const handleClick = () => {
    //     this.setState((prevState) => ({ 
    //         editing: !prevState.editing
    //     }))
    // }

    const { listItem: {id, text, done, editing}, handleEditClick, handleDelete, handleTick, handleChange } = props

    return (
        <div style={done ? liContainerDone : liContainer}>
            { !editing &&
                <div style={contentAreaStyle}>
                    {/* <h1>Title</h1> */}
                    <p style={textStyle}>{text}</p>
                    <div style={buttonGroupStyle}>
                        <button style={{...tickButtonStyle, backgroundColor: "#8dab7f"}} onClick={() => handleTick(id)}>{done ? "UNDO" : "DO"}</button>
                        { !done && 
                            <span>
                                <button style={{...tickButtonStyle, backgroundColor: "#587b7f"}} onClick={() => handleEditClick(id)}>EDIT</button>
                                <button style={{...tickButtonStyle, backgroundColor: "#9b291f"}} onClick={() => handleDelete(id)}>DELETE</button>
                            </span>
                        }
                        
                    </div>
                </div>
            }
            { editing &&
                <div style={contentAreaStyle}>
                    {/* <button onClick={() => handleEditClick(id)}>BACK</button> */}
                    <br/>
                    <input style={liInput} value={text} onChange={(e) => handleChange(id, e.target.value)}/>
                    <button style={{...tickButtonStyle, backgroundColor: "#587b7f"}} onClick={() => handleEditClick(id)}>UPDATE</button>
                </div>
            }
        </div>
    )
    
}

export default ListItem